import React from 'react'
import {SwapiServiceConsumer} from '../context-swapi-service'

export default (mapMethodsToProps) => (WrappedComponent) => {

  const hocComponent = ({ ...props }) => {
    return (
      <SwapiServiceConsumer>
        {(swapiService) => {
          const methods = mapMethodsToProps(swapiService);
          return <WrappedComponent {...methods} {...props} />
        }}
      </SwapiServiceConsumer>
    )
  }

  return hocComponent
}
