
import ItemList from '../item-list'
import {withData, withChildFunction, withSwapiService, compose} from '../hoc-helpers';

const renderFunction = (i) => `${i.name}`;

const mapMethodsToProps = ({getAllPeople}) => {
  return {
    getData: getAllPeople
  }
}

export default compose( withSwapiService(mapMethodsToProps),
                        withData,
                        withChildFunction(renderFunction)
                        )( ItemList);