import './random-planet.css';
import React, { Component } from 'react';
import PropTypes from 'prop-types'
import SwapiService from '../../services/swapi-service';
import Spinner from '../spinner';
import ErrorIndicator from '../error-indicator'

export default class RandomPlanet extends Component {

  static defaultProps = {
    updateInerval: 10000
  }

  static propTypes = {
    updateInerval: PropTypes.number
  }

  state = {
    planet: null,
    hasError: false
  }

  swapiService = new SwapiService();

  updatePlanet = () => {
    const randomId = Math.floor((Math.random() * 20) + 2);
    this.swapiService
      .getPlanet(randomId)
      .then(planet => this.setState({
        planet
        })
      )
      .catch(error => {
        console.error(error);
        this.setState({hasError: true}); 
      })
  }

  componentDidMount () {
    this.updatePlanet();
    const {updateInerval} = this.props;
    this.timerToUpdate = setInterval(this.updatePlanet, updateInerval);
  }

  componentWillUnmount() {
    clearInterval(this.timerToUpdate);
  }

  render() {
    const {planet, hasError} = this.state;
    const content = planet ? <PlanetView planet={planet} /> : <Spinner/>;
    const inner = !hasError ? content : <ErrorIndicator/>
    return (
      <div className="random-planet jumbotron rounded">
        {inner}
      </div>

    );
  }
}

const PlanetView = (
  {
  planet: { id,
            name,
            population,
            rotationPeriod,
            diameter}}
) => {

  return (
    <React.Fragment>
      <img className="random-planet__img planet-image" alt="Planet"
        src={`https://starwars-visualguide.com/assets/img/planets/${id}.jpg`} />
      <div className="random-planet__descr">
        <h4 className="random-planet__title">{name}</h4>
        <ul className="random-planet__list list-group list-group-flush">
          <li className="random-planet__item list-group-item">
            <span className="term">Population</span>
            <span>{population}</span>
          </li>
          <li className="random-planet__item list-group-item">
            <span className="term">Rotation Period</span>
            <span>{rotationPeriod}</span>
          </li>
          <li className="random-planet__item list-group-item">
            <span className="term">Diameter</span>
            <span>{diameter}</span>
          </li>
        </ul>
      </div>
    </React.Fragment>
  )
}
