import React from 'react'
import Row from '../row';
import { PersonList } from '../item-lists';
import { PersonDetails } from '../details';
import ErrorBoundy from '../error-boundry';
import { withRouter } from 'react-router-dom';

const PeoplePage = ({ history, match }) => {

  const leftContent = (<ErrorBoundy>
                        <PersonList onItemSelected={(id) => history.push(id)} />
                      </ErrorBoundy>);

  const rightContent = (<ErrorBoundy>
                        <PersonDetails itemId={match.params.id} />
                      </ErrorBoundy>);

  return (
    <Row leftContent={leftContent} rightContent={rightContent} />
  )
}


export default withRouter(PeoplePage);