import React from 'react'
import Row from '../row';
import {StarshipList} from '../item-lists';
import { StarshipDetails } from '../details';
import {withRouter} from 'react-router-dom';

const StarshipPage = ({history, match}) => {

    const leftContent = <StarshipList onItemSelected={(id) => history.push(id)} />
    const rightContent = <StarshipDetails itemId={match.params.id} />;
    
    return (
      <Row leftContent={leftContent} rightContent={rightContent} />
    )

}

export default withRouter(StarshipPage);