import React from 'react'
import Row from '../row';
import { PlanetList } from '../item-lists';
import { PlanetDetails } from '../details';
import {withRouter} from 'react-router-dom';
import ErrorBoundy from '../error-boundry';

const PlanetPage = ({ history, match }) => {

  const leftContent = <ErrorBoundy> <PlanetList onItemSelected={(id) => history.push(id)} /></ErrorBoundy>;
  const rightContent = <ErrorBoundy> <PlanetDetails itemId={match.params.id} /></ErrorBoundy>;

  return (
    <Row leftContent={leftContent} rightContent={rightContent} />
  )

}

export default withRouter(PlanetPage);


