import ItemList from '../item-list'
import withData from '../hoc-helpers/with-data';
import { withChildFunction, withSwapiService, compose } from '../hoc-helpers';

const renderFunction = (i) => `${i.name}`;

const mapMethodsToProps = ({getAllStarships}) => {
  return {
    getData: getAllStarships
  }
}

export default compose( withSwapiService(mapMethodsToProps),
                        withData,
                        withChildFunction(renderFunction)
                        )(ItemList);