import React from 'react';

import Header from '../header';
import RandomPlanet from '../random-planet';

import './app.css';
import { SwapiServiceProvider } from '../context-swapi-service';
import SwapiService from '../../services/swapi-service';
import ErrorBoundy from '../error-boundry';
import { PeoplePage, PlanetPage, StarshipPage} from '../pages';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

export default class App extends React.Component {

  
  swapiService = new SwapiService();

  render() {

    return (
      <Router>
        <SwapiServiceProvider value={this.swapiService} >

          <div className="star-db" >
            <Header />
            <ErrorBoundy>
            <RandomPlanet  />
            </ErrorBoundy>

            <Switch>
              <Route path='/' render={() => <h3>Wellcome to starDB</h3>}  exact />
              <Route path='/people/:id?' component={PeoplePage} />
              <Route path='/planets/:id?' component={PlanetPage} />
              <Route path='/starships/:id?' component={StarshipPage} />
              <Route render={() => <h6>Page is not found</h6>} />
            </Switch>
          </div>
          
        </SwapiServiceProvider>
      </Router>
    );
  }
}

