export default class SwapiService {

  _base_url=`https://swapi.co/api/`;
  _base_img = `https://starwars-visualguide.com/assets/img/`;

  getImagePerson = (id) => {
    return `${this._base_img}characters/${id}.jpg`
  }

  getImagePlanet = (id) => {
    return `${this._base_img}planets/${id}.jpg`
  }

  getImageStarship = (id) => {
    return `${this._base_img}starships/${id}.jpg`
  }

  getResource = async (url) => {
    const res = await fetch(url);
    if (!res.ok) {
     throw new Error(`Could not fetch ${url}`, `received ${res.status}`)
    }
    const body = await res.json();
    return body;
  }

  getAllPeople = async () => {
    const people = await this.getResource(`${this._base_url}people/`);
      return people.results.map(person => {
        return this._transformPerson(person);
      })
  }

  getAllPlanets = async () => {
    const planets = await this.getResource(`${this._base_url}planets/`);
      return planets.results.map(planet => {
        return this._transformPlanet(planet);
      })
  }

  getAllStarships = async () => {
    const starships = await this.getResource(`${this._base_url}starships/`);
      return starships.results.map(starship => {
        return this._transformStarship(starship);
      })
  }

  getPerson = async (id) => {
    const person = await this.getResource(`${this._base_url}people/${id}/`);
      return this._transformPerson(person);
  }

  getPlanet = async (id) => {
    const planet = await this.getResource(`${this._base_url}planets/${id}/`);
      return this._transformPlanet(planet);
  }

  getStarship = async (id) => {
    const starship = await this.getResource(`${this._base_url}starships/${id}/`);
      return this._transformStarship(starship);
  }

  _extractId = (item) => {
    const idRegExp = /\/([0-9]*)\/$/;
    return item.url.match(idRegExp)[1];
  }

  _transformPerson = (person) => {
    return {
      id: this._extractId(person),
      birthYear: person.birth_year,
      eyeColor: person.eye_color,
      gender: person.gender,
      hairColor: person.hair_color,
      height: person.height,
      homeworld: person.homeworld,
      mass: person.mass,
      name: person.name
    }
  }

  _transformPlanet = (planet) => {
    return {
      id: this._extractId(planet),
      climate: planet.climate,
      diameter: planet.diameter,
      gravity: planet.gravity,
      name: planet.name,
      orbitalPeriod: planet.orbital_period,
      population: planet.population,
      rotationPeriod: planet.rotation_period,
      surfaceWater: planet.surface_water,
      terrain: planet.terrain
  }
  }

  _transformStarship = (starship) => {
    return {
      id: this._extractId(starship),
      cargoCapacity: starship.cargo_capacity,
      consumables: starship.consumables,
      costInCredits: starship.cost_in_credits,
      hyperdriveRating: starship.hyperdrive_rating,
      length: starship.length,
      manufacturer: starship.manufacturer,
      model: starship.model,
      name: starship.name,
      passengers: starship.passengers,
      starship_class: starship.starship_class
  }
  }
}
